import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from '../../shared/userService/auth/auth.guard';
import { CompanyUserPublicComponent } from './company/company_user_public.component';
import { IndividualUserPublicComponent } from './individual/individual_user_public.component';
export const CompanyRoutes: Routes = [{
    path: '',
    children: [
        { path: '', redirectTo: 'company_user', pathMatch: 'full' },
        { path: 'company_user', canActivate: [AuthGuard], data: { role: ['admin'] }, component: CompanyUserPublicComponent },
        { path: 'individual_user', canActivate: [AuthGuard], data: { role: ['admin'] }, component: IndividualUserPublicComponent },
    ]
}
];
